	module.exports = {"data":{"markdownRemark":{"html":"<h2>Add Font Awesome Icons to Our Buttons</h2>\n<p>Font Awesome is a convenient library of icons. These icons are vector graphics, stored in the <code>.svg</code> file format. These icons are treated just like fonts. You can specify their size using pixels, and they will assume the font size of their parent HTML elements.</p>\n<p>Heres how you add an icon:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>i</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>fa fa-thumbs-up<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>i</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>Here is how you would insert that icon on to a button:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>button</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>btn btn-block btn-primary<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>i</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>fa fa-thumbs-up<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>i</span><span class=\"token punctuation\">></span></span> Like<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>button</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<p>Get font awesome on your website:\n<a href='http://fontawesome.io/get-started/' target='_blank' rel='nofollow'>Font awesome - Get started</a></p>\n<p>Check out all the available font awesome icons:\n<a href='http://fontawesome.io/icons/' target='_blank' rel='nofollow'>Icons</a></p>","fields":{"slug":"/miscellaneous/add-font-awesome-icons-to-our-buttons/"},"frontmatter":{"title":"Add Font Awesome Icons to Our Buttons"}}},"pathContext":{"slug":"/miscellaneous/add-font-awesome-icons-to-our-buttons/"}}

